<template>
  <div  style="display: flex;align-items: center">
    <el-form :model="addLove" :rules="addLoveRules" ref="addLoveRef" label-width="100px">
      <h3></h3>
      <el-form-item label="" prop="CName">
        <el-input v-model="addLove.CName" placeholder="" style="width: 70%;" @keyup.enter.native="submitForm('addLoveRef')"></el-input>
      </el-form-item>
      <el-form-item label="" prop="CGender" >
        <el-select v-model="addLove.CGender" placeholder="" style="width: 70%;" @keyup.enter.native="submitForm('addLoveRef')">
          <el-option label="" value=1></el-option>
          <el-option label="" value=2></el-option>
          <el-option label="" value=0></el-option>
        </el-select>
      </el-form-item>
      <h3>TA</h3>
      <el-form-item label="TA" prop="BeCName">
        <el-input v-model="addLove.BeCName" placeholder="" style="width: 70%;" @keyup.enter.native="submitForm('addLoveRef')"></el-input >
      </el-form-item>
      <el-form-item label="TA" prop="BeCGender">
        <el-select v-model="addLove.BeCGender" placeholder="" style="width: 70%;" @keyup.enter.native="submitForm('addLoveRef')">
          <el-option label="" value=1></el-option>
          <el-option label="" value=2></el-option>
          <el-option label="" value=0></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="CContent">
        <el-input type="textarea" :rows="5" v-model="addLove.CContent" placeholder="160" resize="none" style="width: 300px"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('addLoveRef')"></el-button>
        <el-button @click="resetForm('addLoveRef')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      addLove: {
        CName: '',
        CGender: '',
        BeCName: '',
        BeCGender: '',
        CContent: ''
      },
      addLoveRules: {
        CName: [
          { required: true, message: '', trigger: 'blur' },
          { max: 6, message: ' 6 ', trigger: 'blur' }
        ],
        CGender: [
          { required: true, message: '', trigger: 'change' }
        ],
        BeCName: [
          { required: true, message: 'TA', trigger: 'change' }
        ],
        BeCGender: [
          { required: true, message: 'TA', trigger: 'change' }
        ],
        CContent: [
          { required: true, message: '', trigger: 'change' },
          { max: 160, message: ' 160 ', trigger: 'blur' }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate( async (valid) => {
        if (valid) {
          const{ data: res } =  await this.$http.post('post',{
              "sender": this.addLove.CName,
              "sender_sex": this.addLove.CGender,
              "recipient": this.addLove.BeCName,
              "recipient_sex": this.addLove.BeCGender,
              "content": this.addLove.CContent
          })
          console.log(res)
          if (res.code ===1){
            this.$message({
              showClose: true,
              message: '',
              type: 'success',
              duration: 2000
            })
            this.resetForm(formName)
            this.$router.push('./PostList')
          }else{
            this.$message({
              showClose: true,
              message: res.message,
              type: 'error',
              duration: 2000
            })
          }
        } else {
          console.error('error');
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>

<style scoped>
  h3{
    color: #4a4a4b;
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #7c7c7c;
  }
  .el-form-item__label{
    color: #555;
  }
</style>
